@import url(https://fonts.googleapis.com/css?family=Roboto+Mono:100&display=swap);
*{
  font-family: 'Roboto Mono', monospace;
}

body{
  display: flex;
  height: 100vh;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #fff;
  overflow: hidden;
  background: rgb(125,126,125);
  background: -webkit-radial-gradient(center, ellipse cover,  rgba(125,126,125,1) 3%,rgba(56,56,56,1) 100%);
  background: -webkit-radial-gradient(center,  ellipse,  rgba(125,126,125,1) 3%,rgba(56,56,56,1) 100%);
  background: radial-gradient(ellipse at center,  rgba(125,126,125,1) 3%,rgba(56,56,56,1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#7d7e7d', endColorstr='#383838',GradientType=1 );
  
}
.calculator{
    height: 320px;
    width: 235px;
    border-radius: 5px;
    overflow: hidden;
    display: grid;
    grid-template-columns: repeat(4, 25%);
    grid-template-rows: 1fr 48px 48px 48px 48px 48px;
}
:root {
    --bg-button: #f0f0f0;
    --border-button: 1px solid #888;
}

.button{
    font-size: 1.4em;
    background-color: #f0f0f0;
    background-color: var(--bg-button);
    border: none;
    border-right: 1px solid #888;
    border-right: var(--border-button);
    border-bottom: 1px solid #888;
    border-bottom: var(--border-button);
    outline: none;
    cursor: pointer;
}

.button:active{
    background-color: #ccc;
}

.button.double {
    grid-column: span 2;
}

.button.triple {
    grid-column: span 3;
}

.button.operation {
    background-color: #fa8231;
    color: #fff;
    border-right: none;
}

.button.operation:active{
    background-color: #fa8231cc;
}
.display{
    grid-column: span 4;
    background-color: #333;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 20px;
    font-size: 2.1em;
}
